import React from 'react'
import styled from 'styled-components/macro'
import { useTdlib } from './Tdlib'

const Container = styled.div({
  display: 'grid',
  placeItems: 'center',
  height: '100vh'
})

const ChatPicture = styled.div(({ src }) => ({
  height: 400,
  width: 400,
  borderRadius: '50%',
  backgroundImage: `url(${src})`,
  backgroundPosition: 'center',
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'cover'
}))

const Message = styled.div({
  fontSize: '2rem'
})

export default function EmptyChat ({ chatId }) {
  const { client } = useTdlib()
  const [title, setTitle] = React.useState(null)
  const [chatPhoto, setChatPhoto] = React.useState(null)

  React.useEffect(() => {
    let isSubscribed = true

    async function getChat () {
      await client.send({
        '@type': 'getChats',
        chat_list: { '@type': 'chatListMain' },
        offset_order: '9223372036854775807',
        offset_chat_id: chatId,
        limit: 1
      })

      const chat = await client.send({
        '@type': 'getChat',
        chat_id: chatId
      })

      const fileId = chat?.photo?.big?.id

      if (fileId) {
        // downloading the file
        await client.send({
          '@type': 'downloadFile',
          file_id: fileId,
          priority: 16,
          synchronous: true
        })

        // Read the data from local tdlib to blob
        const localFile = await client.send({
          '@type': 'readFile',
          file_id: fileId
        })

        const src = URL.createObjectURL(localFile.data)

        if (isSubscribed) {
          setTitle(chat.title)
          setChatPhoto(src)
        }
      }
    }

    getChat()

    return () => (isSubscribed = false)
  }, [client, chatId])

  return (
    <Container>
      <ChatPicture src={chatPhoto} />
      <Message>Send a picture to '{title}' to view it here</Message>
    </Container>
  )
}
