import React from 'react'
import styled from 'styled-components/macro'
import { useTdlib } from './Tdlib'

const ListContainer = styled.div({
  scrollDirection: 'horizontal'
})

const List = styled.ul({
  listStyle: 'none',
  margin: 0,
  padding: 0
})

const Chat = styled.li({
  padding: 10,
  fontSize: '2rem',
  '&:nth-child(odd)': {
    background: '#ccc'
  }
})

export default function ChatSelection () {
  const { client, setChatId } = useTdlib()
  const [chats, setChats] = React.useState([])

  React.useEffect(() => {
    async function getChats () {
      const response = await client.send({
        '@type': 'getChats',
        chat_list: { '@type': 'chatListMain' },
        offset_order: '9223372036854775807',
        offset_chat_id: 0,
        limit: 10
      })

      response.chat_ids.map(async chatId => {
        const chatInfo = await client.send({
          '@type': 'getChat',
          chat_id: chatId
        })
        setChats(chats => [...chats, { id: chatId, title: chatInfo.title }])
      })
    }

    getChats()
  }, [client])

  return (
    <ListContainer>
      <h1>Select a chat to use as Frame:</h1>
      <List>
        {chats.map(chat => (
          <Chat key={chat.id} onClick={event => setChatId(chat.id)}>
            {chat.title}
          </Chat>
        ))}
      </List>
    </ListContainer>
  )
}
