import React from 'react'
import TdClient from 'tdweb'

const TelegramContext = React.createContext()

function TelegramProvider (props) {
  const [client, setClient] = React.useState()
  const [chatId, setChatId] = React.useState()
  // we need to keep a pointer to chatId since is used in the tdlib callback
  const chatIdRef = React.useRef()
  const [event, setEvent] = React.useState()
  const [history, setHistory] = React.useState([])

  const downloadFile = async fileId => {
    // downloading the file
    await client.send({
      '@type': 'downloadFile',
      file_id: fileId,
      priority: 1,
      synchronous: true
    })

    // Read the data from local tdlib to blob
    return await client.send({
      '@type': 'readFile',
      file_id: fileId
    })
  }

  const getChatHistory = React.useCallback(
    (chatId, from_message_id = 0) => {
      client
        .send({
          '@type': 'getChatHistory',
          chat_id: chatId,
          limit: 100,
          from_message_id
        })
        .then(newHistory =>
          setHistory(history => {
            const uniqueMessages = new Set([...history, ...newHistory.messages])
            return Array.from(uniqueMessages).sort((a, b) => a.date - b.date)
          })
        )
    },
    [client]
  )

  const handleMessages = update => {
    // We can have a new message or one could be deleted
    switch (update['@type']) {
      case 'updateNewMessage':
        if (update?.message?.chat_id === chatIdRef.current) {
          setHistory(history => [...history, update?.message])
        }
        break
      case 'updateDeleteMessages':
        // delete the image from the cache
        if (update.chat_id === chatIdRef.current) {
          update.message_ids.forEach(async messageId => {
            setHistory(history =>
              history.filter(message => message.id !== messageId)
            )
          })
        }

        break
      default:
        break
    }
  }

  const onUpdate = React.useCallback(update => {
    // console.log('***', JSON.stringify(update))
    // We care only of the auth states here
    if (update['@type'] === 'updateAuthorizationState') {
      setEvent(update)
    }

    if (
      update['@type'] === 'updateNewMessage' ||
      update['@type'] === 'updateDeleteMessages'
    ) {
      handleMessages(update)
    }
  }, [])

  React.useEffect(() => {
    console.log('Initializing tdlib')
    const tdlib = new TdClient({
      useTestDC: false,
      readOnly: false,
      verbosity: 3,
      jsVerbosity: 3,
      fastUpdating: true,
      useDatabase: false,
      mode: 'wasm'
    })

    tdlib.onUpdate = onUpdate
    setClient(tdlib)
  }, [onUpdate])

  React.useEffect(() => {
    if (client) {
      client.send({
        '@type': 'setTdlibParameters',
        parameters: {
          '@type': 'tdParameters',
          use_test_dc: false,
          api_id: process.env.REACT_APP_APP_ID,
          api_hash: process.env.REACT_APP_HASH_ID,
          system_language_code: navigator.language || 'en',
          device_model: 'Telegram Frame',
          application_version: '0.1',
          use_secret_chats: false,
          use_message_database: false,
          use_file_database: false,
          files_directory: '/'
        }
      })
    }
  }, [client])

  React.useEffect(() => {
    async function loadHistory () {
      await client.send({
        '@type': 'getChats',
        chat_list: { '@type': 'chatListMain' },
        offset_order: '9223372036854775807',
        offset_chat_id: chatId,
        limit: 1
      })

      // on the first call, tdlib could return just 1 message
      await getChatHistory(chatId)
    }

    if (client && chatId) {
      chatIdRef.current = chatId
      loadHistory()
    }
  }, [client, chatId, getChatHistory])

  return (
    <TelegramContext.Provider
      value={{
        client,
        chatId,
        setChatId,
        event,
        history,
        getChatHistory,
        downloadFile
      }}
      {...props}
    />
  )
}

function useTdlib () {
  const context = React.useContext(TelegramContext)
  if (context === undefined) {
    throw new Error(`useTdlib must be used within a TelegramProvider`)
  }
  return context
}

export { TelegramProvider, useTdlib }
